<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col xs="12" lg="12">
        <b-card>
          <div class="section-heading-top">
            <!-- <b-button type="button" variant="secondary" @click="myModal = true" class="mr-1">Launch demo modal</b-button> -->
            <b-button variant="primary" size="sm"  @click="newGroupModal = true" class="btn-new-group"><i class="fa fa-plus"></i>&nbsp;NEW GROUP</b-button><br>
          </div>
          <div class="site-header-nav-underline"></div>
          <b-alert show variant="secondary"><i class="icon-info icons"></i>&nbsp;Each caregiver can belong to one, and only one, group.</b-alert>
          <!-- <b-alert show variant="warning"><i class="fa fa-warning"></i>&nbsp;Each caregiver can belong to one, and only one, group.</b-alert> -->
          <b-card no-body>
          <b-tabs class="group_tabs" card pills vertical nav-wrapper-class="w-40" v-model="tabIndex[1]">
            <span v-for="(tab, index) in tabs" :key="index">
              <b-tab>
                <template slot="title" class="tab-title">
                  <span class="tab-title">
                    {{tab["group_name"]}}&nbsp;<b-badge class="default-badge" variant="success" v-if="tab['is_default']">Default</b-badge>
                  </span>
                </template>
                  <div class="dash-section">
                    <div class="dash-top-right">
                      <b-dropdown size="sm" id="ddown_primary" right text="OPTIONS" variant="secondary" class="m-2">
                        <b-dropdown-item  @click="editGroupModal = true; selectedGroupName=tab['group_name']; selectedGroupId=tab['id']; isSelectedGroupDefault=tab['is_default'];">Edit Group</b-dropdown-item>
                        <b-dropdown-item @click="selectedGroupIndex=index; deleteSelectedGroup(tab['id']); ">Delete Group</b-dropdown-item>
                      </b-dropdown>
                    </div>
                    <div class= "dash-heading-label">Group</div>
                    <div class= "dash-heading">{{tab["group_name"]}}</div>
                  </div>

                  <div class="dash-section-heading">Group Members</div>
                  <div class="plain-table">
                    <div class="plain-table-heading">
                      <div class ="plain-table-cell-heading plain-table-cell">Name</div>
                      <div class ="plain-table-cell-heading plain-table-cell">Phone</div>
                      <div class ="plain-table-cell-heading plain-table-cell">Email</div>
                    </div>
                    <span v-for="(user, index) in caregivers" :key="index">           
                      <span v-if="user['group_id'] == tab['id'] "> 
                        <div class="plain-table-row">
                          <div class="plain-table-cell">{{user['name']}}</div>
                          <div class="plain-table-cell">{{user['phoneNumber']}}</div>
                          <div class="plain-table-cell">{{user['email']}}</div>
                        </div>
                      </span>
                    </span>
                  </div>
                  
              </b-tab>
            </span>
          </b-tabs>
          </b-card>
        </b-card>
      </b-col>
    </b-row>
    <b-modal title="New Group" class="modal-primary modal-header" v-model="newGroupModal" @ok="newGroupModal = false">
      <div class="standard-modal-heading-instructions">
        Create a new group to organize your caregivers
      </div>
      <!-- <b-form-select 
        :plain="true"
        :options= tabs.map(getGroupDropdownValue)
        value="Please select"
        class="dropdown-CaregiverGroup">
      </b-form-select> -->
      <b-form-input class="input-CaregiverGroup" type="text" placeholder="" v-model="newGroupName" ></b-form-input>
      <b-form-checkbox value="1" v-model="isNewGroupDefault">Make this the Default Group?</b-form-checkbox>

       <div slot="modal-footer">
        <p>
          <b-button size="md" class="float-left mr-1" variant="secondary" @click="closeAddNewGroup()">
            Cancel
          </b-button> 
          <b-button size="md" class="float-right" variant="primary" @click="addNewGroup()">
            OK
          </b-button>
        </p>
      </div>
    </b-modal>
    <b-modal title="Edit Group" class="modal-primary modal-header" v-model="editGroupModal" @ok="editGroupModal = false">
      <div class="standard-modal-heading-instructions">
        Edit a Caregiver group to organize your caregivers
      </div>
      <b-form-input class="input-CaregiverGroup" type="text" placeholder="" v-model="selectedGroupName" ></b-form-input>
      <b-form-checkbox value="1" v-model="isSelectedGroupDefault">Make this the Default Group?</b-form-checkbox>

      <div slot="modal-footer">
        <p>
          <b-button size="md" class="float-left mr-1" variant="secondary" @click="closeUpdateGroup()">
            Cancel
          </b-button> 
          <b-button size="md" class="float-right" variant="primary" @click="editSelectedGroup()">
            Update
          </b-button>
        </p>
      </div>
    </b-modal>
    <div class="vld-parent">
        <loading :active.sync="isLoading" 
        :can-cancel="true" 
        :is-full-page="fullPage"
        color="#06b">
        </loading>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios';
import VueNotifications from 'vue-notifications'
import miniToastr from 'mini-toastr'// https://github.com/se-panfilov/mini-toastr
import {restapiPrefix} from './variables.js'
import router from '../router';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

Vue.use(VueNotifications, options)

export default {
  name: 'CaregiverGroups',
  components: {
    Loading
  },
  notifications: {
    showDeleteGroupErrorMsg: {
      type: VueNotifications.types.error,
      title: 'Delete Group Error',
      message: 'You cannot delete your default group. Please make a different group the default and try again.'
    },
    showDeleteGroupExistUserErrorMsg: {
      type: VueNotifications.types.error,
      title: 'Delete Group Error',
      message: 'You cannot delete a group with caregivers in it. Please move the caregivers to another group and try again.'
    }
  },
  data () {
    return {
      tabIndex: [0, 0],
      // restapiPrefix: "http://localhost:8081",
      // restapiPrefix: "",
      userId: 1,
      newGroupModal: false,
      editGroupModal: false,
      newGroupName: "",
      selectedGroupName: "",
      selectedGroupId: "",
      isNewGroupDefault: 0,
      isSelectedGroupDefault: 0,
      selectedGroupIndex: 0,
      tabs: [],
      caregivers: [],
      isLoading: false,
      fullPage: true,
    }
  },
  beforeCreate: function () {
    if(localStorage.user_id == undefined || localStorage.user_id == "0")
    {        
      router.push({ name: "Login"})
    }  
  },  
  created() {
    this.userId = localStorage.user_id;
    this.isLoading = true;
    // Get Caregiver groups by logged in user's Id
    this.getGroupListByUserId(this.userId);
    this.getCaregiverListByUserId(this.userId);
  },
  methods: {
    // getGroupDropdownValue(item) {
    //   var option = { value: "", text: ""};
    //   option.value = item.id;
    //   option.text = item.tabName;
    //   // console.log(option)
    //   return option;
    // },
    getGroupListByUserId(userId) {
      // Get Caregiver groups by logged in user's Id
      let asksUri = restapiPrefix+'/caregiverGroups/getGroupsByUserID/'+userId;
      this.axios.get(asksUri).then(response => {
        this.tabs = response.data;
        this.isLoading = false;
      });
    },
    getCaregiverListByUserId(userId) {
      // Get Caregiver groups by logged in user's Id
      this.caregivers= [];
      let asksUri = restapiPrefix+'/caregivers/getCaregiversByUserID/'+userId;
      this.axios.get(asksUri).then(response => {
        for(let i = 0; i < response.data.length; i++) {
          let caregiver = response.data[i];
          caregiver['name'] = response.data[i]['first_name']+", " +response.data[i]['last_name'];
          caregiver['phoneNumber'] = response.data[i]['phone_number'];
          caregiver['email'] = response.data[i]['email_address'];
          caregiver['group_id'] = response.data[i]['caregiver_group'];

          this.caregivers.push(caregiver);
        }
      });
    },
    deleteSelectedGroup(groupId) {
      if (this.tabs[this.selectedGroupIndex]["is_default"] == 1 ) {
        this.showDeleteGroupErrorMsg();
      } else {
        // get users using groupid
        // -------------------
        // Axios: Get user infos by group_id using selectedGroupId 
        //
        // ------------------- 
        this.isLoading = true;
        var hasUser = false;
        for(let i=0; i<this.caregivers.length; i++) {
          console.log(this.caregivers[i])
          if (this.caregivers[i].group_id == groupId) {
            this.showDeleteGroupExistUserErrorMsg();
            this.isLoading = false;
            hasUser = true;
            break;
          }
        }
        if(!hasUser) {
          let asksUri = restapiPrefix+'/caregiverGroups/'+groupId;
          this.axios.delete(asksUri).then(response => {
            miniToastr.success('Deleted Group Successfully!', 'Success');
            this.getGroupListByUserId(this.userId);
          });
        }
      }

      // let asksUri = 'http://localhost:8081/caregiverGroups/'+groupId;
      // this.axios.delete(asksUri).then(response => {
      //   this.getGroupListByUserId(1);
      // });
    },
    addNewGroup() {
      // console.log("sdfsd")
      if (this.newGroupName != "") {
        this.isLoading = true;
        let asksUri = restapiPrefix+'/caregiverGroups/';
        axios.post(asksUri, {
          group_name: this.newGroupName,
          is_default: this.isNewGroupDefault,
          user_id: this.userId
        }) 
        .then(response => {
          miniToastr.success('Added Group Successfully!', 'success');
          this.getGroupListByUserId(this.userId);
        })
        .catch(function (error) {
          miniToastr.error('something went wrong!', 'Error');
        });
        
      }

      this.newGroupModal = false;
      this.newGroupName = "";
      this.isNewGroupDefault = 0;
    },
    editSelectedGroup() {
      console.log('tesstt');
      if (this.selectedGroupName != "") {
        this.isLoading = true;
        let asksUri = restapiPrefix+'/caregiverGroups/'+this.selectedGroupId;
        axios.put(asksUri, {
          group_name: this.selectedGroupName,
          is_default: this.isSelectedGroupDefault,
          user_id: this.userId
        }) 
        .then(response => {
          miniToastr.success('Edited Group successfully!', 'Success');
          this.getGroupListByUserId(this.userId);
          // console.log(response);
        })
        .catch(function (error) {
          miniToastr.error('something went wrong!', 'Error');
          console.log(error);
        });
      }

      this.editGroupModal = false;
    },
    closeAddNewGroup() {
      this.newGroupModal = false;
      this.newGroupName = "";
      this.isNewGroupDefault = 0;
    },
    closeUpdateGroup() {
      // console.log(this.selectedGroup)
      this.editGroupModal = false;
    }
  },
  
}
</script>

